.px2-modal {
  background: rgba(0, 0, 0, 0.7);
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 1000000;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: var(--px2-font-family, "Hiragino Kaku Gothic ProN", "Hiragino Sans", "Meiryo", "Helvetica Neue", "Arial", sans-serif);
  padding: 0;
  margin: 0;
  border: none;
  container-type: inline-size;
}
.px2-modal--no-title .px2-modal__header {
  display: none;
}
.px2-modal--no-btn .px2-modal__footer {
  display: none;
}
.px2-modal--background {
  background: transparent;
}
.px2-modal > form {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.px2-modal__close {
  position: absolute;
  right: 10px;
  top: 8px;
  width: 40px;
  height: 40px;
  z-index: 1150;
}
.px2-modal__close button {
  position: absolute;
  left: 0;
  right: 0;
  display: block;
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
  border-radius: 0;
  border: none;
  background-color: transparent;
  color: #999;
  cursor: pointer;
}
.px2-modal__close button::before, .px2-modal__close button::after {
  content: "";
  border-bottom: 1px solid #000;
  width: 70%;
  position: absolute;
  top: 50%;
  left: 10%;
}
.px2-modal__close button::before {
  transform: rotate(45deg);
}
.px2-modal__close button::after {
  transform: rotate(-45deg);
}
.px2-modal__close button:hover {
  border: none;
}
.px2-modal__close button:hover::before, .px2-modal__close button:hover::after {
  border-bottom: 1px solid #666;
}
.px2-modal__close button[data-px2-modal-locked] {
  opacity: 0.2;
}
.px2-modal__dialog {
  position: relative;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  color: #333;
  color: var(--px2-text-color, #333);
  background-color: #f9f9f9;
  background-color: var(--px2-background-color, #f9f9f9);
  box-sizing: border-box;
  padding: 10px 0;
  margin: 20px auto;
  width: calc(100% - 40px);
  max-width: 520px;
  height: auto;
  max-height: calc(100% - 40px);
  overflow: visible;
  border-radius: 5px;
  font-size: medium;
  animation: px2-modal--initial 0.3s ease 0s 1 normal none running;
  transition: transform 0.3s, opacity 0.3s;
  container-type: inline-size;
}
.px2-modal__dialog--closed {
  transform: translateY(-10px);
  opacity: 0;
}
.px2-modal__header {
  padding: 18px 40px 8px 20px;
  margin-top: -10px;
  margin-bottom: 0;
  z-index: 1100;
}
.px2-modal__body {
  position: relative;
  top: 0;
  left: 0;
  padding: 0 20px;
  flex-grow: 1;
  box-sizing: border-box;
  z-index: 1050;
}
.px2-modal__body-inner {
  display: block;
  box-sizing: border-box;
  container-type: inline-size;
}
.px2-modal__body, .px2-modal--content-fill .px2-modal__body-inner {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  overflow: auto;
}
.px2-modal__body > *, .px2-modal--content-fill .px2-modal__body-inner > * {
  flex-grow: 100;
}
.px2-modal--content-fill .px2-modal__body {
  padding: 0;
}
.px2-modal__title {
  font-weight: bold;
  outline: none;
}
.px2-modal__footer {
  margin: 0 0 -10px 0;
  padding: 10px 0 12px 0;
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  flex-wrap: nowrap;
  flex-shrink: 0;
  gap: 30px;
  overflow: auto;
  max-height: 30vh;
  z-index: 1100;
}
.px2-modal__footer-primary, .px2-modal__footer-secondary {
  box-sizing: content-box;
  width: fit-content;
  padding: 0 20px;
}
.px2-modal__footer-primary > ul, .px2-modal__footer-secondary > ul,
.px2-modal__footer > ul {
  display: flex;
  justify-content: flex-end;
  flex-wrap: nowrap;
  gap: 5px;
  margin: 0;
  padding: 0;
}
.px2-modal__footer-primary > ul > li, .px2-modal__footer-secondary > ul > li,
.px2-modal__footer > ul > li {
  list-style-type: none;
  margin: 0;
  padding: 0;
  white-space: nowrap;
}
.px2-modal__footer-secondary > ul {
  justify-content: flex-start;
}

@keyframes px2-modal--initial {
  0% {
    transform: translateY(-10px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}
@keyframes px2-modal--initial-drawer-left {
  0% {
    transform: translateY(0);
    opacity: 1;
    left: -100%;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
    left: 0;
  }
}
@keyframes px2-modal--initial-drawer-right {
  0% {
    transform: translateY(0);
    opacity: 1;
    right: -100%;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
    right: 0;
  }
}
.px2-modal.px2-modal--type-drawer-left,
.px2-modal.px2-modal--type-drawer-right {
  background: rgba(0, 0, 0, 0.1);
}
.px2-modal.px2-modal--type-drawer-left .px2-modal__dialog,
.px2-modal.px2-modal--type-drawer-right .px2-modal__dialog {
  position: absolute;
  top: 0;
  left: auto;
  right: auto;
  width: 360px;
  max-width: calc(100% - 20px);
  height: 100%;
  max-height: 100%;
  margin: 0;
  border-radius: 0;
}
.px2-modal.px2-modal--type-drawer-left .px2-modal__dialog--closed,
.px2-modal.px2-modal--type-drawer-right .px2-modal__dialog--closed {
  transform: translateY(0);
  opacity: 1;
}

.px2-modal.px2-modal--type-drawer-left .px2-modal__dialog {
  left: 0;
  animation: px2-modal--initial-drawer-left 0.3s ease 0s 1 normal none running;
  transition: transform 0.3s, opacity 0.3s, left 0.3s;
}
.px2-modal.px2-modal--type-drawer-left .px2-modal__dialog--closed {
  left: -100%;
}

.px2-modal.px2-modal--type-drawer-right .px2-modal__dialog {
  right: 0;
  animation: px2-modal--initial-drawer-right 0.3s ease 0s 1 normal none running;
  transition: transform 0.3s, opacity 0.3s, right 0.3s;
}
.px2-modal.px2-modal--type-drawer-right .px2-modal__dialog--closed {
  right: -100%;
}

@keyframes px2-loading {
  0% {
    transform: scale(0);
